//widget header

.widget {
  background: $light-blue;
  margin-bottom: $xl-spacing;
  &-header {
    background: $blue;
    color: $off-white;
    padding: $m-size;
    display: flex;
    justify-content: space-between;

    &__title {
      margin: 0;
    }
  }

  &__message {
      color: $off-white;
      margin: 0;
      padding: $l-spacing;
      text-align: center;
      border-bottom: 1px solid lighten($light-blue, 10%);
  }
}
