.add-option {
  display: flex;
  flex-direction: column;
  padding: $m-size;
  &-error {
    color: $off-white;
    font-style: italic;
    margin: $m-size 0 0 0;
    padding: 0 $m-size;
  }

  &__input {
    background: $dark-blue;
    border: none;
    border-bottom: 0.3rem solid darken($dark-blue, 10%);
    flex-grow: 1;
    // margin-right: $s-size;
    margin: 0 0 $s-size 0;
    color: $off-white;
    padding: $s-size;
  }
}
